/* PUT STYLES HERE */
@media all { 

    /* RESETTING*/
    *{
        margin: 0;
        padding: 0;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
    }

    .spa-wrapper{
        background: #000;
        text-align: center;
        font-family: 'VT323', monospace;
        overflow: hidden;
        color: #fff;
        font-size: 16px;
        height: 100vh;
        width: 100vw;
    }

    /* GAME SCREEN */
    .gameField {
        position: relative;
        -webkit-transform: translate(-10%, 0);
                transform: translate(-10%, 0);
        width: 100vw;
        height: 100px;
        top: 0;
        left: 0;
    }

    nav {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
        padding: 15px;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    }

    .game-stats-text {
        font-size: 1.8rem;
        -webkit-user-select: none;
           -moz-user-select: none;
            -ms-user-select: none;
                user-select: none;
    }

    .icon{
        color: rgb(0, 191, 255);;
        -webkit-animation: hue 2s infinite ease;;
                animation: hue 2s infinite ease;
    }

    .icon:before{
        vertical-align: text-bottom!important;
    }

    .targetIcon {
        width: 100%;
        height: auto;
        fill: rgb(0, 191, 255);
        position: absolute;
        -webkit-animation: hue 2s infinite ease-in-out, rotation 4s infinite linear alternate;
                animation: hue 2s infinite ease-in-out, rotation 4s infinite linear alternate;
        outline: none;
        -webkit-transition: width 0.1s ease-in, height 0.1s ease-in;
        transition: width 0.1s ease-in, height 0.1s ease-in;
        path {
            cursor: pointer;
        }
    }

    .wrap {
        width: 10%;
        position: absolute;
        -webkit-transition: top 1.5s linear, left 1.5s linear, -webkit-transform 0.1s ease-in;
        transition: top 1.5s linear, left 1.5s linear, -webkit-transform 0.1s ease-in;
        transition: transform 0.1s ease-in, top 1.5s linear, left 1.5s linear;
        transition: transform 0.1s ease-in, top 1.5s linear, left 1.5s linear, -webkit-transform 0.1s ease-in;
    }

    .wrap.clicked {
        -webkit-transform-origin: center;
                transform-origin: center;
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
        .targetIcon{
            fill: white;
            width: 100%;
            
        }
    }

    /* WELCOME SCREEN && WINNER/LOST SCREEN */ 

    .welcome-winning-screen{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        width: 100vw;
        height: 100vh;
    }

    .welcome-winning-text {
        font-size: 2.5rem;
        width: 65%;
        line-height: 1.5em;
        letter-spacing: 0.2em;
        -ms-flex-item-align: center;
            align-self: center;
        text-align: center;
        -webkit-user-select: none;
           -moz-user-select: none;
            -ms-user-select: none;
                user-select: none;
    }

    .restart-button{
        font-family: 'VT323', monospace;
        font-size: 1.5rem;
        background-color: transparent;
        color: #fff;
        border: 2px solid #fff;
        border-radius: 5px;
        margin-top: 30px;
        padding: 15px 20px;
        cursor: pointer;
        -webkit-user-select: none;
           -moz-user-select: none;
            -ms-user-select: none;
                user-select: none;
        outline: none;
    }

    .restart-button:hover{
        -webkit-animation: hue 1s infinite ease;
                animation: hue 1s infinite ease;
        background-color: rgb(0, 191, 255);
        color:  #fff;
    }


    /* ANIMATIONS */ 
    @-webkit-keyframes hue {
        from {
            -webkit-filter: hue-rotate(0deg);
                    filter: hue-rotate(0deg);
        }

        to {
            -webkit-filter: hue-rotate(360deg);
                    filter: hue-rotate(360deg);
        }
    } 
    @keyframes hue {
        from {
            -webkit-filter: hue-rotate(0deg);
                    filter: hue-rotate(0deg);
        }

        to {
            -webkit-filter: hue-rotate(360deg);
                    filter: hue-rotate(360deg);
        }
    }

    @-webkit-keyframes rotation {
        from {
            -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
        }
        to {
            -webkit-transform: rotate(359deg);
                    transform: rotate(359deg);
        }
    }

    @keyframes rotation {
        from {
            -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
        }
        to {
            -webkit-transform: rotate(359deg);
                    transform: rotate(359deg);
        }
    }
}

@media (max-width: 768px) { 
    /* GAME SCREEN */
    .wrap {
        width: 12%;
    }
}

@media (max-width: 750px) { 
    /* GAME SCREEN */
    .wrap {
        width: 15%;
    }
}

@media (max-width: 576px) {  
    /* GAME SCREEN */
    nav {
        -webkit-box-orient: vertical;
        -webkit-box-direction: reverse;
            -ms-flex-direction: column-reverse;
                flex-direction: column-reverse;
        text-align: left;
    }
    .wrap {
        width: 18%;
    }
    .game-stats-text {
        line-height: 1.5em;
        font-size: 1.5rem;
    }

}

@media (max-width: 420px) {  
    /* GAME SCREEN */
    .game-stats-text {
        font-size: 1.2rem;
    }
    .wrap {
        width: 25%;
    }
}



